import React from 'react'
import './Rate.css';
import Calk from "../Calk/Calk";

class Rate extends React.Component {
    rates;
    constructor(props) {
        super(props);
        this.state = {
            'date': '',
            'currencyRate': {},
            'curs': ''
        }
        this.currency = ['USD', 'RUB', 'CAD', 'CZK', 'UAH'];
        this.getRate();
    }


    getRate = () => {
        fetch('https://api.exchangerate.host/latest')
            .then(data => {
                return data.json();
            })
            .then(data => {
                console.log(data[this.currency]);
                this.setState({date: data.date})
                const result = {};
                for (let i = 0; i < this.currency.length; i++) {
                    result[this.currency[i]] = data.rates[this.currency[i]]
                }
                // this.state.curs = result[this.currency[0]];
                let cureser = result[this.currency[0]];
                this.setState({curs: cureser});
                this.setState({currencyRate: result});

            });
    }

    render() {
        let str = this.state.date;
        let newstr = str.replace(/-/g, " ");
        let new2 = newstr.replace(/(\w+)\s(\w+)\s(\w+)/, '$3 $2 $1');
        let new3 = new2.replace(/ /g, "-");
        console.log(new3);
        return (
            <div className="rate">
                <h3>Курс валют на: {new3} </h3>
                <div className="flex-container">
                    {Object.keys(this.state.currencyRate).map((keyName, i) => (
                            <div className="block flex-item" key={keyName}>

                                <div className="currency-name">{keyName}</div>
                                <div className="currency-in"><span
                                    className="currency-name-mini">EUR</span> {this.state.currencyRate[keyName].toFixed(2)}
                                </div>
                                <div className="currency-out"><span
                                    className="currency-name-mini">USD</span> {(this.state.currencyRate[keyName].toFixed(2) / this.state.curs).toFixed(2)}
                                </div>

                            </div>
                        )
                    )}
                    {console.log(this.state.curs)}
                </div>
                <div className="calculator">
                    <Calk rate={this.state.currencyRate} />
                </div>
            </div>
        );
    }
}

export default Rate;
