import React from 'react'
import './Calk.css';

class Calk extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            result: 0
        }
    }
    static getDerivedStateFromProps(props) {
        return {rate: props.rate}
    }

    calcRate = (e) => {
        e.preventDefault();
        console.log('hay');
        let elements = e.target.elements;
        console.log(elements['count-currency'].value)
        console.log(elements['type-currency'].value)

        let countCurrency = elements['count-currency'].value;
        let typeCurrency = elements['type-currency'].value;
        this.setState({result: (countCurrency / this.state.rate[typeCurrency].toFixed(2)).toFixed(2)});
        console.log(this.state.rate[typeCurrency].toFixed(2))
    }

    render() {
        return (
            <div className="container">
                <h3>Калькулятор обмена</h3>
                <div className="block">
                    <div>Я хочу</div>
                    <div><label><input type="radio" name="radio" value="0" />Купить</label></div>
                    <div><label><input type="radio" name="radio" value="0" />Продать</label></div>
                    <div>
                        <form onSubmit={this.calcRate}>
                        <input type="number" defaultValue="150" name="count-currency" />
                        <select name="type-currency" id="">
                            {Object.keys(this.props.rate).map((keyName, i) =>
                                (
                                    <option key={keyName} value={keyName}>{keyName}</option>
                                )
                            )}
                        </select>
                            <input type="submit" defaultValue="calk" />
                    </form>
                    </div>
                    <div>
                        <h4>Результат</h4>
                        <ul className="calc-res">
                            <li>EUR {this.state.result}</li>

                        </ul>
                    </div>
                </div>
            </div>
        );
    }
}

export default Calk;
